import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class HttpiceandfireService {

  public characterBaseUrl = 'https://www.anapioficeandfire.com/api/characters';
  public bookBaseUrl = 'https://www.anapioficeandfire.com/api/books';
  public housesBaseUrl = 'https://www.anapioficeandfire.com/api/houses';

  constructor(private _http: HttpClient) { }

  public getCharacterInfo(): any{

    let myResponse = this._http.get(this.characterBaseUrl);
    return myResponse;
    
  }
  public getSingleCharacterInfo(baseUrl):any{
    let myResponse = this._http.get(baseUrl);
    return myResponse;
  }
  public getBookInfo(): any{

    let myResponse = this._http.get(this.bookBaseUrl);
    return myResponse;
  }
  public getSingleBookInfo(baseUrl):any{
    let myResponse = this._http.get(baseUrl);
    return myResponse;
  }
  public getHousesInfo(): any{

    let myResponse = this._http.get(this.housesBaseUrl);
    return myResponse;
    
  }
  public getSingleHouseInfo(baseUrl):any{
    let myResponse = this._http.get(baseUrl);
    return myResponse;
  }
}
