import { Component, OnInit } from '@angular/core';

import { ActivatedRoute } from '@angular/router';
import { HttpiceandfireService } from '../httpiceandfire.service';

@Component({
  selector: 'app-character-detail',
  templateUrl: './character-detail.component.html',
  styleUrls: ['./character-detail.component.css']
})
export class CharacterDetailComponent implements OnInit {

  public singleCharacterInfo;

  constructor(private _route: ActivatedRoute, private httpiceandfire: HttpiceandfireService) { }

  ngOnInit() {
    let currentUrl = this._route.snapshot.paramMap.get('url');
    this.httpiceandfire.getSingleCharacterInfo(currentUrl).subscribe(

      data =>{
        this.singleCharacterInfo = data;
        console.log(this.singleCharacterInfo);
      },
      error =>{
        alert(error.errorMessage);
      }
    )
    
  }

}
