import { Component, OnInit } from '@angular/core';
import { HttpiceandfireService } from '../httpiceandfire.service';
import { ActivatedRoute } from '../../../node_modules/@angular/router';

@Component({
  selector: 'app-book-detail',
  templateUrl: './book-detail.component.html',
  styleUrls: ['./book-detail.component.css']
})
export class BookDetailComponent implements OnInit {

  public singleBookInfo;

  constructor(private httpiceandfire:HttpiceandfireService, private _route:ActivatedRoute) { }

  ngOnInit() {
    let currentUrl = this._route.snapshot.paramMap.get('url');
    this.httpiceandfire.getSingleBookInfo(currentUrl).subscribe(
      data=>{
        this.singleBookInfo = data;
        console.log(this.singleBookInfo);
      },
      error=>
      {
        alert(error.errorMessage);
      }
    )
  }

}
