import { Component, OnInit } from '@angular/core';
import {ActivatedRoute } from '@angular/router';
import { HttpiceandfireService } from '../httpiceandfire.service';


@Component({
  selector: 'app-house-detail',
  templateUrl: './house-detail.component.html',
  styleUrls: ['./house-detail.component.css']
})
export class HouseDetailComponent implements OnInit {

   public  singleHouseInfo;

  constructor(private _route: ActivatedRoute, private httpiceandfire: HttpiceandfireService) { }

  ngOnInit() {

    let currentUrl = this._route.snapshot.paramMap.get('url');
    this.httpiceandfire.getSingleHouseInfo(currentUrl).subscribe(

      data=>{
        this.singleHouseInfo = data;
        console.log(this.singleHouseInfo);
      },
      error=>{
        alert(error.errorMessage);
      }
      
    )
  }

}
