import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { CharacterDetailComponent } from './character-detail/character-detail.component';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { NgHttpLoaderModule } from 'ng-http-loader';
import { HttpiceandfireService } from './httpiceandfire.service';
import { BookDetailComponent } from './book-detail/book-detail.component';
import { HouseDetailComponent } from './house-detail/house-detail.component';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    CharacterDetailComponent,
    BookDetailComponent,
    HouseDetailComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    NgHttpLoaderModule,
    RouterModule.forRoot([
      {path: 'home', component:HomeComponent},
      {path:'', component:HomeComponent},
      {path:'character/:url', component:CharacterDetailComponent},
      {path:'book/:url',component:BookDetailComponent},
      {path:'house/:url',component:HouseDetailComponent}
    
    ])
  ],
  providers: [HttpiceandfireService,{provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule { }
