import { Component, OnInit } from '@angular/core';
import { HttpiceandfireService } from '../httpiceandfire.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {

  public allCharacter;
  public allBook;
  public allHouse;

  constructor(private httpIceandFire: HttpiceandfireService) { }

  ngOnInit() {
    this.httpIceandFire.getCharacterInfo().subscribe(
      data => {
        this.allCharacter = data;
        this.allCharacter.sort( function(ar1,ar2){
          if(ar1.aliases < ar2.aliases){
            return -1;
          }else if(ar1.aliases > ar2.aliases){
            return 1;
          }else{
            return 0;
          }
        });
        console.log(this.allCharacter);
      },
      error => {
        alert('some error occured');
      }
    )

    this.httpIceandFire.getBookInfo().subscribe(
      data => {
        this.allBook = data;
        this.allBook.sort( function(ar1,ar2){
          if(ar1.name < ar2.name){
            return -1;
          }else if(ar1.name > ar2.name){
            return 1;
          }else{
            return 0;
          }
        });

        console.log(this.allBook);
      },
      error => {
        alert('some error occured');
      }
    )

    this.httpIceandFire.getHousesInfo().subscribe(
      data => {
        this.allHouse = data;
        this.allHouse.sort( function(ar1,ar2){
          if(ar1.name < ar2.name){
            return -1;
          }else if(ar1.name > ar2.name){
            return 1;
          }else{
            return 0;
          }
        });

        console.log(this.allHouse);
      },
      error => {
        alert('some error occured');
      }
    )

  }

}
